import React, { useState, useEffect, useRef } from "react";
import { BsCloudUpload } from "react-icons/bs";
import { Link, useParams, useNavigate } from "react-router-dom";
import Headertwo from "../components/Headertwo";
import { Footer } from "../components/Footer";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import "react-tabs/style/react-tabs.css";
import SuccessPop from "../components/SuccessPop";
import { Modal, Button } from "react-bootstrap";
import axios from "axios";
import { useTranslation } from "react-i18next";
import i18n from "../i18n";
import { AlertDelete } from "../components/AlertDelete";
import { HiDotsVertical } from "react-icons/hi";
import { UserPhotoLikeComment } from "../components/UserPhotoLikeComment";
import { IconStarFilled } from "@tabler/icons-react";
import MediaRenderer from "../components/MediaRenderer";
export default function PrivateMedia() {
  const navigate = useNavigate();
  const [showPopSuccess, setShowPopSuccess] = useState(false);
  const [successPopMessage, setPopsuccessMessage] = useState("");
  const [selectedId, setSelectedId] = useState("");
  const [movepostdata, setmovepostdata] = useState("");
  const [mainAlbumname, setmainAlbumname] = useState("");
  const [albumnameMoveto, setalbumnameMoveto] = useState([]);
  const [opemModalmove, setopemModalmove] = useState(false);
  const [spinner, setspinner] = useState(false);
  const [spinnerdis, setspinnerdis] = useState(true);
  const [successmsg, setsuccessmsg] = useState("");
  const [mainId, setmainId] = useState("");
  const [deleteMessage, setdeleteMessage] = useState("");
  const [isPopupOpen_delete, setisPopupOpen_delete] = useState(false);
  const [viewalbum, setviewalbum] = useState(false);
  const [isallmedia, setallmedia] = useState([]);
  const [modalOpen, setModalOpen] = useState(false);
  const [currentSlideIndex, setCurrentSlideIndex] = useState(1);
  const [isGrouppost, setIsGrouppost] = useState([]);
  const [albumimages, setAlbumimages] = useState([]);
  const [mediaImages, setmediaImages] = useState([]);
  const [mediaVideos, setmediaVideos] = useState([]);
  const [LoginData, setLoginData] = useState(null);
  const [opemModalupload, setopemModalupload] = useState(false);
  const [albumId, setalbumId] = useState("");
  const [error, setError] = useState("");
  const [images, setImages] = useState(null);
  const { id } = useParams();
  const [Albumname, setAlbumname] = useState("");
  //const [modalOpen, setModalOpen] = useState(false);
  // Component Like comment
  const [userId, setuserId] = useState("");
  const [UserAlbumId, setUserAlbumId] = useState("");
  const [openFile, setopenFile] = useState("");
  const [currentIndex, setCurrentIndex] = useState(0);
  // Component Like comment
  const closeModal = () => setModalOpen(false);
  const storedUsername = localStorage.getItem("userLogin");
  const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
  console.log(userLogin.id, id);
  const showSlide = (n) => {
    if (n > images.length) setCurrentSlideIndex(1);
    else if (n < 1) setCurrentSlideIndex(images.length);
    else setCurrentSlideIndex(n);
  };
  useEffect(() => {
    if (modalOpen) {
      // Disable scrolling when modal is open
      document.body.style.overflowY = "hidden";
    } else {
      // Enable scrolling when modal is closed
      document.body.style.overflowY = "auto";
    }

    // Cleanup to restore the overflow style when the component unmounts
    return () => {
      document.body.style.overflowY = "auto";
    };
  }, [modalOpen]);
  var apiUrl = "https://backend.amourette.no/api/members/";
  useEffect(() => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null; // Safely parse login data
    setLoginData(userLogin);
  }, []);

  useEffect(() => {
    if (id) {
      getprivatemediaalbum(id);
    }
  }, [id]);
  useEffect(() => {
    if (id) {
      checkaccessTopage(id);
    }
  }, [id]);
  const checkaccessTopage = async (id) => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    let formData = {
      user_id: userLogin.id,
      albumid: id,
    };
    console.log(formData);
    try {
      const res = await axios.post(apiUrl + "checkaccessuserAlbum", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Correct content type for file uploads
        },
      });

      var respo = res.data;

      if (respo.row.length === 0) {
        try {
          const res = await axios.post(apiUrl + "checkaccessTopage", formData, {
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json", // Correct content type for file uploads
            },
          });

          var respo = res.data;
          console.log(respo);
          if (respo.status !== "1") {
            navigate("/dashboard");
          }
        } catch (err) {
          // Enhanced error handling
          if (err.response) {
            console.error("Response error:", err.response);
          } else if (err.request) {
            console.error("Request error:", err.request);
          } else {
            console.error("General error:", err.message);
          }
        }
      }
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
        console.error("Response error:", err.response);
      } else if (err.request) {
        console.error("Request error:", err.request);
      } else {
        console.error("General error:", err.message);
      }
    }
  };
  const getprivatemediaalbum = async (id) => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null; // Safely parse login data
    let formData = {
      user_id: userLogin.id,
      album_id: id,
    };

    try {
      const res = await axios.post(apiUrl + "getprivatemediaalbum", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data;
      console.log(respo.results);
      if (respo.results.length > 0) {
        setmainId(respo.results[0].m_id);
        setAlbumname(respo.results[0].name);
        setuserId(respo.results[0].user_id);
        setmainAlbumname(respo.results[0].name);
        let images = [];
        let videos = [];

        let media = [];

        respo.results.forEach((item) => {
          try {
            const mediaArray = JSON.parse(item.images); // safely parse item.images JSON string

            if (Array.isArray(mediaArray)) {
              mediaArray.forEach((mediaUrl) => {
                // Create a media object for each file
                const mediaObj = {
                  url: mediaUrl,
                  id: item.m_id, // main item ID
                  type: null, // we'll set type below
                  cover_image: item.cover_image,
                };

                // Only add if type is determined (ignore unknown file types)
                if (mediaObj) {
                  media.push(mediaObj);
                }
              });
            }
          } catch (error) {
            console.error("Error parsing images JSON:", error);
          }
        });
        console.log(respo.results);
        setmediaImages(media);
        setmediaVideos(videos);
        setCurrentIndex(0);
      } else {
        // navigate("/friendsearch");
      }
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        //console.error("Request data:", err.request);
      } else {
        // console.error("Error message:", err.message);
      }
    }
  };

  const getallmedia = async (album_id) => {
    let formData = {
      album_id: album_id,
    };

    try {
      const res = await axios.post(apiUrl + "getallmediaa", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });

      if (res && res.data && res.data.results) {
        const redata = res.data.results;

        // Parsing the images array and updating state
        const updatedMedia = redata.map((item) => {
          let mediaArray = [];

          // Check if the 'images' field exists and is a valid string
          if (
            item.images &&
            item.images !== "undefined" &&
            item.images !== ""
          ) {
            try {
              mediaArray = JSON.parse(item.images); // Parse the string into an array
            } catch (e) {
              console.error("Error parsing images for item:", item.id, e);
              mediaArray = []; // In case of an error, use an empty array
            }
          }

          // Collect media items, separating images and videos
          const mediaItems = mediaArray.filter(
            (media) =>
              media.match(/\.(jpeg|jpg|png|gif|webp)$/i) || // Check for images
              media.match(/\.(mp4|mov|avi|mkv|webm|ogg|flv|3gp|mkv|webm)$/i) // Check for videos
          );

          return {
            mediaItems: mediaItems, // Store the parsed media array
          };
        });
        setviewalbum(true);
        //console.log(updatedMedia);
        // Set the parsed media data in state
        setallmedia(updatedMedia);
      }
    } catch (err) {
      if (err.response) {
        console.error("Server Response Error:", err.response.data);
      } else if (err.request) {
        console.error("No Response Received:", err.request);
      } else {
        console.error("Request Error:", err.message);
      }
    }
  };
  const openModal = (mediaArray, index) => {
    setIsGrouppost(mediaArray); // Set the full list of images or videos
    setCurrentSlideIndex(index); // Set the clicked item's index
    setModalOpen(true);
  };
  const { t } = useTranslation();

  useEffect(() => {
    const savedLanguage = localStorage.getItem("language") || "en"; // Default to 'en' if no language is saved
    //console.log("language");
    //console.log(savedLanguage);

    // Change the language if it's not already the current language
    if (i18n.language !== savedLanguage) {
      i18n.changeLanguage(savedLanguage);
    }
  }, [i18n, t]);
  const handledeletebtnn = async () => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null; // Safely parse login data
    let formData = {
      user_id: userLogin.id,
      image: albumId,
      id: mainId,
    };

    try {
      const res = await axios.post(apiUrl + "privatealbumdelete", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data;
      setisPopupOpen_delete(false);
      getprivatemediaalbum(id);
    } catch (err) {}
  };
  const handledeletebtn = async (idd) => {
    setalbumId(idd);
    setisPopupOpen_delete(true);
    setdeleteMessage("Are you sure? You want delete this");
  };
  const handlecoverphoto = async (urlpost) => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    let formData = {
      id: id,
      user_id: userLogin.id,
      coverphoto: urlpost,
    };
    try {
      const res = await axios.post(apiUrl + "setCoverphoto", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      setShowMenu(false);
      setShowPopSuccess(true);
      setPopsuccessMessage("Cover photo has been set successfully.");
      getprivatemediaalbum(id);
      setTimeout(() => {
        setShowPopSuccess(false);
      }, 800);
    } catch (err) {
      if (err.response) {
        console.error("Server Response Error:", err.response.data);
      } else if (err.request) {
        console.error("No Response Received:", err.request);
      } else {
        console.error("Request Error:", err.message);
      }
    }
  };

  const handlemovetobtn = async (post) => {
    setopemModalmove(true);
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    let formData = {
      id: id,
      user_id: userLogin.id,
    };
    setmovepostdata(post);
    try {
      const res = await axios.post(apiUrl + "getmediaAlbumName", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      setalbumnameMoveto(res.data.results);
    } catch (err) {
      if (err.response) {
        console.error("Server Response Error:", err.response.data);
      } else if (err.request) {
        console.error("No Response Received:", err.request);
      } else {
        console.error("Request Error:", err.message);
      }
    }
  };
  const closePopup_delete = () => {
    setisPopupOpen_delete(false);
  };
  const refreshpopup = async () => {
    handledeletebtnn();
  };
  const handleupload = () => {
    setopemModalupload(true);
  };
  const handleModalClose = () => {
    setopemModalupload(false);
    setopemModalmove(false);
  };
  const handleSubmitupload = async (e) => {
    e.preventDefault();
    if (!images) {
      alert("Please select images to upload.");
      return;
    }
    setspinnerdis(false);
    setspinner(true);
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    const formData = new FormData();

    images.forEach((file) => {
      formData.append("images", file); // Use the key 'files' for your backend to handle multiple files
    });
    console.log(images);
    formData.append("id", mainId);
    formData.append("user_id", userLogin.id);
    formData.append("fileType", "");
    try {
      const res = await axios.post(apiUrl + "uploadprivateAlbums", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "multipart/form-data",
        },
      });
      var respo = res.data;
      setsuccessmsg(respo.message);
      setTimeout(() => {
        setspinnerdis(true);
        setspinner(false);
        setopemModalupload(false);
        setImages(null);
        getprivatemediaalbum(id);
        setsuccessmsg("");
      }, 3500);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const handleFileChange = (e) => {
    const selectedFiles = e.target.files;
    const maxFileSize = 50 * 1024 * 1024; // 10 MB in bytes
    const maxFileCount = 10; // Maximum of 10 files
    let validFiles = [];
    let fileError = "";

    // Check if the number of files exceeds the limit
    if (selectedFiles.length > maxFileCount) {
      fileError = `You can only upload a maximum of ${maxFileCount} files.`;
    } else {
      Array.from(selectedFiles).forEach((file) => {
        const fileType = file.type;

        // Allow only images and videos
        if (!fileType.startsWith("image/") && !fileType.startsWith("video/")) {
          fileError = `File "${file.name}" is not an image or video.`;
        } else if (file.size > maxFileSize) {
          fileError = `File "${file.name}" exceeds the 5 MB limit.`;
        } else {
          validFiles.push(file);
        }
      });
    }

    if (fileError) {
      setError(fileError);
    } else {
      setError("");
      setImages(validFiles);
    }
  };
  const handleSubmitmove = async () => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null; // Safely parse login data
    let formData = {
      file: movepostdata,
      id: selectedId,
      removeid: mainId,
      user_id: userLogin.id,
    };

    try {
      const res = await axios.post(apiUrl + "movetoFile", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      console.log(res.data);
      setSelectedId("");
      setopemModalmove(false);
      setTimeout(() => {
        setShowPopSuccess(true);
      }, 80);

      setPopsuccessMessage("Successfully moved");
      getprivatemediaalbum(id);
      setTimeout(() => {
        setShowPopSuccess(false);
      }, 800);
    } catch (err) {}
  };
  const handlechooseId = (event) => {
    setSelectedId(event.target.value); // Update state with selected ID
  };
  const [showMenu, setShowMenu] = useState(false);
  const toggleMenu = (index) => {
    setShowMenu(showMenu === index ? null : index);
  };
  //Like Comments, Album

  const [PopupData, setPopupData] = useState(null);
  const [PopupDatafile, setPopupDatafile] = useState(null);
  const [isPopupOpen11, setIsPopupOpen11] = useState(false);
  const [UserPhotoCommetss, setUserPhotoCommetss] = useState([]);
  const closePopup11 = () => {
    setIsPopupOpen11(false);
    document.body.style.overflowY = "auto";
  };
  const openPopup11 = () => {
    setIsPopupOpen11(true);
    document.body.style.overflowY = "hidden";
  };
  const openModalLikeComment = async (id, file, index) => {
    setUserAlbumId(id);
    setopenFile(file);
    setCurrentIndex(index);
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null; // Safely parse login data
    let formData = {
      file: file,
      id: id,
      user_id: userId,
    };
    try {
      const res = await axios.post(apiUrl + "getuseralbumFile", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      const datacheck = res.data;
      //console.log(datacheck);
      if (datacheck.matched === true) {
        setPopupData(datacheck.results);
        setPopupDatafile(file);

        getUseralbumsComments(file, id);
        openPopup11();
      }
    } catch (err) {}
  };
  const getUseralbumsComments = async (file, id) => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null; // Safely parse login data
    let formData = {
      file: file,
      id: id,
      user_id: userId,
    };
    try {
      const res = await axios.post(apiUrl + "getUseralbumsComments", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      const datacheck = res.data;
      console.log(datacheck.album);
      setUserPhotoCommetss(datacheck.album.comments);
    } catch (err) {}
  };
  const getfavalbum = async () => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null; // Safely parse login data
    let formData = {
      file: openFile,
      id: UserAlbumId,
      user_id: userId,
    };
    console.log(formData);
    try {
      const res = await axios.post(apiUrl + "getuseralbumFile", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      const datacheck = res.data;
      if (datacheck.matched === true) {
        setPopupData(datacheck.results);
        setPopupDatafile(openFile);

        getUseralbumsComments(openFile, UserAlbumId);
        openPopup11();
      }
    } catch (err) {}
  };
  const goToNext = () => {
    if (currentIndex < mediaImages.length - 1) {
      const nextIndex = currentIndex + 1;
      const nextMedia = mediaImages[nextIndex];
      setCurrentIndex(nextIndex);
      openModalLikeComment(nextMedia.id, nextMedia.url, nextIndex);
    }
  };

  const goToPrevious = () => {
    if (currentIndex > 0) {
      const prevIndex = currentIndex - 1;
      const prevMedia = mediaImages[prevIndex];
      setCurrentIndex(prevIndex);
      openModalLikeComment(prevMedia.id, prevMedia.url, prevIndex);
    }
  };
  const refreshpost = () => {
    window.location.reload();
  };
  const fileInputRef = useRef(null);
  const removeFile = (index) => {
    setImages((prev) => {
      const updatedImages = prev.filter((_, i) => i !== index);
      // If no files left, reset the input value
      if (updatedImages.length === 0 && fileInputRef.current) {
        fileInputRef.current.value = "";
      }
      return updatedImages;
    });
  };
  return (
    <>
      <Headertwo />
      {showPopSuccess && <SuccessPop message={successPopMessage} />}
      <AlertDelete
        isOpen1={isPopupOpen_delete}
        onClose1={closePopup_delete}
        message={deleteMessage}
        onReturnSent={refreshpopup}
      ></AlertDelete>
      <section className="ggboxes d-block gapy">
        <div className="container-lg">
          <div className="row gy-4 mb-5">
            <div className="col-md-6">
              <h1 className="pagetitle">{`${t("Photos")}`}</h1>
              <strong>({Albumname})</strong>
            </div>
            <div className="col-md-6">
              <div className="d-flex justify-content-md-end"></div>
            </div>
          </div>
          <div className="row">
            <div className="col-12">
              <Tabs>
                <TabList>
                  <div className="d-flex align-items-center justify-content-between w-100">
                    <div className="d-flex align-items-center gap-2">
                      <Tab>{`${t("Media")}`}</Tab>
                      <button
                        type="button"
                        onClick={() => navigate(`/friend/${userId}`)}
                        className="btn backbtn"
                      >
                        Back
                      </button>
                    </div>

                    <button
                      className="react-tabs__tab react-tabs__tab--selected"
                      type="button"
                      onClick={handleupload}
                    >
                      {t("Upload")}
                    </button>
                  </div>
                </TabList>

                <TabPanel>
                  <div className="row gy-4">
                    {mediaImages.length > 0 ? (
                      mediaImages.map((post, index) => (
                        <div className="col-md-3" key={index}>
                          <div className="d-flex flex-column gap-2">
                            <div className="ggimagebox ofit position-relative">
                              {/* If it is an image */}
                              <MediaRenderer
                                className="hover-shadow"
                                url={post.url}
                                onClick={() =>
                                  openModalLikeComment(post.id, post.url, index)
                                }
                              />

                              {/* Show edit options if user is owner */}
                              {post.url === post.cover_image && (
                                <>
                                  {userLogin.id === userId && (
                                    <div className="d-flex flex-column gap-3 image_center_ w-100">
                                      <div
                                        style={{
                                          position: "relative",
                                          display: "inline-block",
                                        }}
                                        className="d-flex justify-content-between align-items-center gap-3 w-100"
                                      >
                                        {post.url === post.cover_image && (
                                          <button
                                            className="py-1"
                                            type="button"
                                            title={t("Cover_photo")}
                                          >
                                            <IconStarFilled />
                                          </button>
                                        )}
                                        <button
                                          className="remove_mess"
                                          onClick={() => toggleMenu(index)}
                                        >
                                          <HiDotsVertical />
                                        </button>

                                        {/* Popup menu */}
                                        {showMenu === index && (
                                          <ul className="popup-menu w-fit">
                                            <li
                                              onClick={() =>
                                                handledeletebtn(post.url)
                                              }
                                            >
                                              {t("delete")}
                                            </li>
                                            <li
                                              onClick={() =>
                                                handlemovetobtn(post.url)
                                              }
                                            >
                                              {t("MOve_to")}
                                            </li>
                                          </ul>
                                        )}
                                      </div>
                                    </div>
                                  )}
                                </>
                              )}
                              {post.url != post.cover_image && (
                                <>
                                  {userLogin.id === userId && (
                                    <div className="d-flex flex-column gap-3 image_center_ w-100">
                                      <div
                                        style={{
                                          position: "relative",
                                          display: "inline-block",
                                        }}
                                        className="d-flex justify-content-end align-items-end gap-3 w-100"
                                      >
                                        <button
                                          className="remove_mess"
                                          onClick={() => toggleMenu(index)}
                                        >
                                          <HiDotsVertical />
                                        </button>

                                        {/* Popup menu */}
                                        {showMenu === index && (
                                          <ul className="popup-menu w-fit">
                                            <li
                                              onClick={() =>
                                                handledeletebtn(post.url)
                                              }
                                            >
                                              {t("delete")}
                                            </li>
                                            <li
                                              onClick={() =>
                                                handlemovetobtn(post.url)
                                              }
                                            >
                                              {t("MOve_to")}
                                            </li>
                                          </ul>
                                        )}
                                      </div>
                                    </div>
                                  )}
                                </>
                              )}
                            </div>
                          </div>
                        </div>
                      ))
                    ) : (
                      // If no media available
                      <p>{`${t("No_avalibale_media")}`}.</p>
                    )}
                  </div>
                </TabPanel>
                <TabPanel>
                  <div className="row gy-4">
                    {mediaVideos.length > 0 ? (
                      mediaVideos.map((post, index) => (
                        <div className="col-md-3" key={index}>
                          <div className="d-flex flex-column gap-2">
                            <div className="ggimagebox ofit">
                              <video
                                onClick={() => openModal(mediaVideos, index)} // Pass media array & index
                                controls
                                alt="video"
                              >
                                <source src={post} type="video/mp4" />
                                {`${t("Your_browser_does")}`}
                              </video>
                              <div className="d-flex flex-column gap-3 image_center_">
                                <div
                                  style={{
                                    position: "relative",
                                    display: "inline-block",
                                  }}
                                >
                                  <button
                                    className="remove_mess"
                                    onClick={() => toggleMenu(index)}
                                  >
                                    <HiDotsVertical />
                                  </button>

                                  {showMenu === index && (
                                    <ul className="popup-menu w-fit">
                                      <li onClick={() => handledeletebtn(post)}>
                                        {" "}
                                        {t("delete")}
                                      </li>
                                      <li onClick={() => handlemovetobtn(post)}>
                                        {t("MOve_to")}
                                      </li>
                                    </ul>
                                  )}
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      ))
                    ) : (
                      <p>{`${t("No_avalibale_media")}`}.</p>
                    )}
                  </div>
                </TabPanel>
                <TabPanel>
                  <div className="row gy-4">
                    {
                      !viewalbum && albumimages.length > 0
                        ? albumimages.map((album, index) => (
                            <div className="col-md-3" key={index}>
                              <div className="d-flex flex-column gap-2">
                                <div className="ggimagebox ofit">
                                  <img
                                    src={album.img} // You can choose to show the first image as a sample
                                    alt="Media"
                                    onClick={() =>
                                      getallmedia(album.album_id, index)
                                    } // Pass media array & index
                                    className="hover-shadow"
                                  />
                                  <span>
                                    {album.album_name} ({album.media_count})
                                  </span>
                                </div>
                              </div>
                            </div>
                          ))
                        : !viewalbum && <p>{`${t("No_avalibale_media")}`}.</p> // Show message when viewalbum is true and no media is available
                    }

                    {viewalbum &&
                      (isallmedia.length > 0 ? (
                        isallmedia.map((album, albumIndex) =>
                          album.mediaItems.length > 0 ? (
                            album.mediaItems.map((media, mediaIndex) => (
                              <div
                                className="col-md-3"
                                key={`${albumIndex}-${mediaIndex}`}
                              >
                                <div className="d-flex flex-column gap-2">
                                  <div className="ggimagebox ofit">
                                    {media.match(
                                      /\.(mp4|mov|avi|mkv|webm|ogg|flv|3gp|mkv|webm)$/i
                                    ) ? (
                                      <video
                                        controls
                                        onClick={
                                          () =>
                                            openModal(
                                              album.mediaItems,
                                              mediaIndex
                                            ) // Pass the media array & index
                                        }
                                        className="hover-shadow"
                                      >
                                        <source src={media} type="video/mp4" />
                                        {`${t("Your_browser_does")}`}
                                      </video>
                                    ) : (
                                      <img
                                        src={media} // Display the image
                                        alt="Media"
                                        onClick={
                                          () =>
                                            openModal(
                                              album.mediaItems,
                                              mediaIndex
                                            ) // Pass the media array & index
                                        }
                                        className="hover-shadow"
                                      />
                                    )}
                                  </div>
                                </div>
                              </div>
                            ))
                          ) : (
                            <p>{`${t("No_avalibale_media")}`}.</p>
                          )
                        )
                      ) : (
                        <p>{`${t("No_avalibale_media")}`}.</p>
                      ))}
                  </div>
                </TabPanel>
              </Tabs>
            </div>
          </div>
        </div>
      </section>

      {/* pop up code */}
      {modalOpen && (
        <div id="myModal" className="modalgg">
          <span className="close cursor" onClick={() => setModalOpen(false)}>
            &times;
          </span>
          <div className="modal-content">
            {isGrouppost.length > 0 ? (
              isGrouppost.map((post, index) => (
                <div
                  className="mySlides"
                  style={{
                    display: currentSlideIndex === index ? "block" : "none",
                  }}
                  key={index}
                >
                  <div className="numbertext">
                    {index + 1} / {isGrouppost.length}
                  </div>

                  {post.match(/\.(jpeg|jpg|gif|png|webp|bmp|svg)$/i) ? (
                    <img src={post} alt="img" />
                  ) : post.match(/\.(mp4|mov|avi|mkv|webm|ogg|flv|3gp)$/i) ? (
                    <video controls>
                      <source src={post} type="video/mp4" />
                      {t("Your_browser_does")}
                    </video>
                  ) : null}
                </div>
              ))
            ) : (
              <p>{t("Noresult")}</p>
            )}

            {/* Navigation Arrows */}
            <a
              className="prev"
              onClick={() =>
                setCurrentSlideIndex((prev) =>
                  prev === 0 ? isGrouppost.length - 1 : prev - 1
                )
              }
            >
              &#10094;
            </a>
            <a
              className="next"
              onClick={() =>
                setCurrentSlideIndex((prev) =>
                  prev === isGrouppost.length - 1 ? 0 : prev + 1
                )
              }
            >
              &#10095;
            </a>
          </div>
        </div>
      )}
      <Modal
        show={opemModalupload}
        className="edit_admin"
        onHide={handleModalClose}
      >
        <Modal.Header closeButton>
          <Modal.Title className="textcolor">{t("Upload")}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <form onSubmit={handleSubmitupload}>
            <div className="mb-3">
              <div className="uploadfile rel bgload">
                <p className="pb-2 text-dark">{t("select_one_more")}</p>
                <input
                  type="file"
                  name="file"
                  required
                  accept="image/*,video/mp4,video/webm,video/ogg"
                  multiple
                  onChange={handleFileChange}
                  className="fileload"
                  ref={fileInputRef}
                />
                {error && <p style={{ color: "red" }}>{error}</p>}
                <div className="inload" style={{ height: "50px" }}>
                  <div className="d-flex gap-3 align-items-center">
                    {images !== null && (
                      <>
                        {images.map((file, index) => (
                          <div className="upimage rel" key={index}>
                            <button
                              type="button"
                              className="preremovBtn"
                              onClick={() => removeFile(index)}
                            >
                              X
                            </button>
                            <div className="inimag ofit">
                              {file.type.startsWith("image/") ? (
                                <img
                                  src={URL.createObjectURL(file)}
                                  alt={file.name}
                                />
                              ) : (
                                <video width="160" height="120" controls>
                                  <source
                                    src={URL.createObjectURL(file)}
                                    type={file.type}
                                  />
                                </video>
                              )}
                            </div>
                          </div>
                        ))}
                      </>
                    )}
                    <div className="d-flex align-items-center gap-2">
                      <BsCloudUpload style={{ fontSize: "24px" }} />
                      <h3
                        className="m-0 text-dark"
                        style={{ fontSize: "14px" }}
                      >
                        {t("upload_image")}/{t("Videos")}
                      </h3>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <span className="text-success">{successmsg}</span>
            {spinner && (
              <div
                className="spinner-border text-success send_load"
                role="status"
              >
                <span className="visually-hidden">{t("loading")}...</span>
              </div>
            )}
            <Modal.Footer>
              <Button variant="secondary" onClick={handleModalClose}>
                {t("Close")}
              </Button>
              <Button
                type="submit"
                style={{
                  background: "#6e75a8",
                  opacity: !spinnerdis ? 0.1 : 1,
                  pointerEvents: !spinnerdis ? "none" : "auto",
                }}
                className=""
              >
                {t("Upload")}
              </Button>
            </Modal.Footer>
          </form>
        </Modal.Body>
      </Modal>
      <Modal
        show={opemModalmove}
        className="edit_admin"
        onHide={handleModalClose}
      >
        <Modal.Header closeButton>
          <Modal.Title className="textcolor"> {t("Upload")}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <form
            onSubmit={handleSubmitmove}
            method="post"
            action="javascript:void(0)"
          >
            <div className="row g-3">
              {" "}
              {/* Added spacing between rows */}
              <div className="col-md-12">
                <label className="stextlabel text-dark">
                  {t("AlbumNameFrom")}{" "}
                </label>
                <input
                  type="text"
                  disabled
                  name="album_name"
                  value={mainAlbumname}
                  className="form-control"
                  required
                />
              </div>
              <div className="col-md-12">
                <label className="stextlabel text-dark">{t("MOve_to")} *</label>
                <select
                  required
                  className="form-control"
                  onChange={handlechooseId}
                  name="move_to"
                >
                  <option value="">--Select--</option>
                  {albumnameMoveto.map((album, index) => (
                    <option key={index} value={album.id}>
                      {album.name}
                    </option>
                  ))}
                </select>
              </div>
            </div>

            <span className="text-success">{successmsg}</span>
            {spinner && (
              <div
                className="spinner-border text-success send_load"
                role="status"
              >
                <span className="visually-hidden">{t("loading")}...</span>
              </div>
            )}
            <Modal.Footer>
              <Button variant="secondary" onClick={handleModalClose}>
                {t("Close")}
              </Button>
              <Button
                type="submit"
                style={{
                  background: "#6e75a8",
                }}
                className=""
              >
                {t("Move")}
              </Button>
            </Modal.Footer>
          </form>
        </Modal.Body>
      </Modal>
      {/* pop up code */}
      <Footer />
      <UserPhotoLikeComment
        isOpen1={isPopupOpen11}
        onClose1={closePopup11}
        PopupData={PopupData}
        PopupDatafile={PopupDatafile}
        UserPhotoCommetss={UserPhotoCommetss}
        UserAlbumId={UserAlbumId}
        getfavalbum={getfavalbum}
        goToNext={goToNext}
        goToPrevious={goToPrevious}
        mediaImages={mediaImages} // Pass mediaImages
        currentIndex={currentIndex} // Pass currentIndex
        setCurrentIndex={setCurrentIndex} // Pass setter if updating in child
        openModalLikeComment={openModalLikeComment} // Pass to update popup
        refreshpost={refreshpost}
      />
    </>
  );
}
